body{
	background:url("../overlays/overlay3.svg"), #e9ecef;
	background-position: center center, center center;
    background-size: auto, cover;
	overflow: hidden;
}
.padding{
	display: flex;
	background:#e9ecef;
	justify-content: space-around;
	padding: 10px;
}
.active{
	font-size: 1.1rem;
	font-weight: bold;
}

a{
	color: rgb(0, 0, 0);
	transition: all linear 0.15s;
}

a:hover{
	color: rgb(100, 100, 100);
	text-decoration: none;
}

.jumbotron {
	background: none;
}

.btnClear{
	font-family: "Roboto",
	Arial,
	Helvetica,
	sans-serif;
	float: right;
	font-size: 24px;
	padding: 5px;
	min-width: 150px;
	color: #fff;
	border: 4px solid #212529;
	border-radius: 10px;
	background: rgb(228, 73, 255);
	background: linear-gradient(45deg, rgba(33, 90, 210, 0.7) 0%, rgba(228, 73, 255, 0.7) 100%);
}