@import url(https://fonts.googleapis.com/css2?family=Lato:ital@0;1&family=Roboto&display=swap);
h1{
	font-family: "Lato", Arial, Helvetica, sans-serif;
	font-size: 5em;
}

body{
	background:url(/static/media/overlay3.30feb6c2.svg), #e9ecef;
	background-position: center center, center center;
    background-size: auto, cover;
	overflow: hidden;
}
.padding{
	display: flex;
	background:#e9ecef;
	justify-content: space-around;
	padding: 10px;
}
.active{
	font-size: 1.1rem;
	font-weight: bold;
}

a{
	color: rgb(0, 0, 0);
	transition: all linear 0.15s;
}

a:hover{
	color: rgb(100, 100, 100);
	text-decoration: none;
}

.jumbotron {
	background: none;
}

.btnClear{
	font-family: "Roboto",
	Arial,
	Helvetica,
	sans-serif;
	float: right;
	font-size: 24px;
	padding: 5px;
	min-width: 150px;
	color: #fff;
	border: 4px solid #212529;
	border-radius: 10px;
	background: rgb(228, 73, 255);
	background: linear-gradient(45deg, rgba(33, 90, 210, 0.7) 0%, rgba(228, 73, 255, 0.7) 100%);
}
.vertical-center {
	min-height: 100%;
    min-height: 90vh;
	display: flex;
	align-items: center;
}
.btnClassify {
	font-family: "Roboto", Arial, Helvetica, sans-serif;
	font-size: 24px;
	padding: 5px;
	min-width: 150px;
	color: #fff;
	background: rgb(33, 90, 210);
	background: linear-gradient(45deg, rgba(33, 90, 210, 1) 0%, rgba(228, 73, 255, 1) 100%);
	border: 4px solid #212529;
	border-radius: 10px;
}

.form-control{
	border: 4px solid #212529;
	border-radius: 10px;
	box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.075) inset, 0px 0px 8px rgba(80, 80, 80, 0.5);

}

.form-control:focus {
	border: 4px solid #65727e;
	box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.075) inset, 0px 0px 8px rgba(151, 217, 255, 0.5);
}

.custom-form-control{
	border:none;
	outline: none !important;
}
.form-control:focus{
	border-color: #66afe9;
    outline: 0;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
}
.custom-select:focus{
	border-color: #66afe9;
    outline: 0;
    box-shadow: inset 0 1px 1px rgba(0,0,0,.075),0 0 8px rgba(102,175,233,.6);
}
.input-group-text {
	min-width: 160px !important;
	display: inline-block !important
}
.btnTune {
	font-family: "Roboto", Arial, Helvetica, sans-serif;
	font-size: 24px;
	padding: 5px;
	min-width: 125px;
	color: #fff;
	background: rgb(33, 90, 210);
	background: linear-gradient(45deg, rgba(33, 90, 210, 1) 0%, rgba(228, 73, 255, 1) 100%);
	border: 4px solid #212529;
	border-radius: 10px;
}

.form-control {
	border-radius: 10px;
	box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.075) inset, 0px 0px 8px rgba(80, 80, 80, 0.5);

}

.form-control:focus {
	border: 1px solid #65727e;
	box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.075) inset, 0px 0px 8px rgba(151, 217, 255, 0.5);
	border-radius: 10px;
}

